import React, { useEffect, useState } from "react";
import $ from "jquery";
import { Map, MouseTool , Marker } from "react-amap";
import { CLINIC_MAP_CODE } from "./../Type";

const layerStyle = {
  padding: "10px",
  background: "#fff",
  border: "1px solid #ddd",
  borderRadius: "4px",
  position: "absolute",
  top: "10px",
  left: "10px",
};

const Amap = ({datas , mapData : pushMapData }) => {
  const [states, setStates] = useState({});
  const [tool, setTool] = useState();
  const [position , setPosition] = useState({});
  useEffect(() => {

    setPosition(datas ? datas : '')
    setStates({
      map: {
        lat: 0,
        long: 0,
      },
      what: "点击下方按钮开始绘制",
    });
  }, []);

  const toolEvents = {
    created: (tool) => {
      setTool(tool);
    },
    draw({ obj }) {
      const map = {
        latitude: obj.getPosition().lat,
        longitude: obj.getPosition().lng,
      };
      // console.log("dataaaaaaa : ", map);
      let mapDataJson = JSON.stringify(map);
      // console.log("map data : " , mapDataJson)
      pushMapData(mapDataJson);
      drawWhat(obj);
    },
  };

  function drawWhat(obj) {
    let text = "";
    switch (obj.CLASS_NAME) {
      case "AMap.Marker":
        text = `你绘制了一个标记，坐标位置是 {${obj.getPosition()}}`;
        break;

      default:
        text = "";
    }
    setStates({
      what: text,
    });
  }

  function drawMarker() {
    if (tool) {
      tool.marker();
      setStates({
        what: "准备绘制坐标点",
      });
    }
  }

  function close() {
    if (tool) {
      tool.close();
    }
    setStates({
      what: "关闭了鼠标工具",
    });
  }

  const mapPlugins = ["ToolBar"];
  const mapCenter = { longitude: 120, latitude: 35 };

  return (
    <div>
      <div style={{ width: "100%", height: 370 }}>
        <Map
          amapkey={"styles/afdd93fc70e330480ae6a54d3d8d4e79"}
          plugins={mapPlugins}
          center={mapCenter}
          setMapStyle={"styles/afdd93fc70e330480ae6a54d3d8d4e79"}
        >
          <Marker position={position} />
          <MouseTool events={toolEvents} />
          <div style={layerStyle}>{states.what}</div>
        </Map>
      </div>
      <div className="col s12" style={{ marginTop: 20 }}>
        <button
          class="btn waves-effect blue darken-4"
          style={{ textTransform: "unset" }}
          onClick={() => {
            drawMarker();
          }}
        >
          Draw Marker
        </button>
        <button
          style={{ marginLeft: 20, textTransform: "unset" }}
          class="btn waves-effect red darken-4"
          onClick={() => {
            close();
          }}
        >
          Close Draw Marker
        </button>
      </div>
    </div>
  );
};

export default Amap;
