// add clinic component
// doctor mitavanad az inja clinic ezafe va update konad
import React, {memo, useEffect, useState} from "react"; // componentha va hock ha react
import Amap from "./Amap"; // component Amap
import SideBar from "../Partials/SideBar"; // component sidebar doctor
import Header from "../../partials/Header"; // component header public
import ReactDOM from "react-dom"; // reactDOM baray setkardan component amap dar tag #map
import {NavLink} from "react-router-dom";
import $ from "jquery"; // library jquery
import {GetToken} from "../../../../services/Authentication/GetToken"; // check doctor token
import axios from "axios"; // library axios for get va post data (file) to server
import M from "materialize-css"; // library mateirlize-css
import {CLINIC_MAP_CODE} from "./Type"; // code localstorage mapData
import {UPLOADED_URL} from "./../../../../services/Connection/Types";
import {
    UploadedFile,
    GetDoctorClient,
    GetAllDoctorClinic,
    UpdateClinicAddress,
    AddClinicApi,
    DeleteClinic,
    GetMe,
} from "./../../../../entity/Redux/Doctors/Action"; // call kardan action uploaded
import {useDispatch, useSelector} from "react-redux";

// library estefadeh shode baray traslation
import {useTranslation} from "react-i18next";

const FILE_CODE = "clinic_img_1254";
export default memo(() => {
    // clinic address data state
    const [clinicAddress, setClinicAddress] = useState();
    // select file state
    const [selectFile, setSelectFile] = useState();
    const [clinicLocation, setClinicLocation] = useState({});
    const [allClinic, setAllClinic] = useState(); // save All doctors clinic data
    const [singleClinic, setSingleClinic] = useState(); // save single doctors clinic data
    const [pickUpFile, setPickUpFile] = useState(false); // check for uploaded file
    const [resUpload, setResUpload] = useState(); // get ans set result uloaded file

    // get state from redux reducer

    // create object from useDispatch => dispatch kardan action
    const dispatch = useDispatch();
    const Doctor = useSelector((state) => state);

    const DoctorData = Doctor.Doctor;
    console.log("clinic data old : ", DoctorData);
    useEffect(() => {
        $("#back_shado").fadeOut();

        // get all doctor's clinic
        dispatch(GetDoctorClient());
        // get doctor detail
        dispatch(GetMe());

        // render kardan map dar tag id=map
        // ReactDOM.render(, document.getElementById("map"));
        // daryaft lat va long az component amap va localstorage
        const mapData = JSON.parse(localStorage.getItem(CLINIC_MAP_CODE));

        //set kardan mapData dar state ClinicAddress
        setClinicAddress({
            ...clinicAddress,
            location: {},
        });

        console.log("result uploade : ", Doctor.Doctor);
        if (Doctor.Doctor.UploadedRes) {
            if (Doctor.Doctor.UploadedRes.data) {
                setResUpload(Doctor.Doctor.UploadedRes.data.data);
            }
        }
        // use materialize code
        var mateilizeBox = document.querySelectorAll(".materialboxed");
        M.Materialbox.init(mateilizeBox);
    }, []);

    const {t, i18n} = useTranslation();

    {
        /*
        -- language
        -- do zabaneh kardan site
        */
    }

    function handleLang(e) {
        // e.preventDefault();
        // sabt type language dar storage
        if (!localStorage.getItem("lan")) {
            localStorage.setItem("lan", "en");
        }
        //daryaft type language as localstorage
        var lan = localStorage.getItem("lan");
        var ckeckbox = $("input[name=cahngelan]");
        if (ckeckbox.is(":checked") && lan === "en") {
            localStorage.setItem("lan", "ch");
        } else {
            localStorage.setItem("lan", "en");
        }
        let lang = localStorage.getItem("lan");
        // gharar dadan language dar library translate
        i18n.changeLanguage(lang);
    }

    //handle change file input

    function handleChangeFile(event) {
        // console.log("fles : ", e.target.files[0]);
        setSelectFile(event.target.files[0]); // daryaft etelat file va sabt dar state
        setPickUpFile(true);
    }

    // handle file upload

    function upload_file() {
        const formData = new FormData(); // new kardan formData
        $("#uploaded_img_clinic").fadeIn(); // fadeIn backLoading
        // if (selectFile) {
        // check konim ke file add shode ya na
        formData.append("file", selectFile); // append kardan filename be formdata

        $(".loading_progress").fadeIn();
        setTimeout(() => {
            dispatch(UploadedFile(formData));
        }, 300);

        // }
    }

    // end language

    // set kardan data clinic dar variable
    var clinicDefaulValue = {};
    // check kardan defined bodan clinic
    if (Doctor.Doctor.resDocClinic) {
        clinicDefaulValue = Doctor.Doctor.resDocClinic.data.data[0];
    }

    // handle kardan inputhai address tavasot karbar
    const HandleClinicAddress = (e) => {
        // set kardan vorodi ha dar state
        setClinicAddress({
            ...clinicAddress,
            [e.target.name]: e.target.value,
        });
    };

    //handle kardan add clinic
    const HandleAddClinic = (e) => {
        e.preventDefault();
        var attach = [];
        if (clinicAddress.name !== "") {
            if (localStorage.getItem(FILE_CODE)) {
                if (clinicLocation && clinicLocation !== "") {
                    const img_path = JSON.parse(localStorage.getItem(FILE_CODE)).data.data;
                    if (img_path) {
                        // add new clinic image to attachment old
                        attach[attach.length] = img_path;
                        // for live change clinic old data
                        var fitData = {...clinicAddress};
                        // add new attach to clinic data
                        fitData.attachment = attach;
                        // add location to clinic data
                        fitData.location = clinicLocation;
                        // console.log("dataaaaaaa imgggg : ", fitData);
                        dispatch(AddClinicApi(fitData));
                    }
                } else {
                    M.toast({html: 'added location for clinic!'})
                }
            } else {
                M.toast({html: 'added image for clinic!'})
                // dispatch(AddClinicApi(fitData));
            }
        } else {
            M.toast({html: 'added name for clinic!'})
        }
    };

    let locati = clinicDefaulValue ? clinicDefaulValue.location : "";
    // handle kardan form update clinic
    const handleForm = (e, id) => {
        e.preventDefault();

        console.log("clinic adresss one : ", clinicDefaulValue);
        var attach = []; // new attay for attachment img
        if (clinicDefaulValue) {
            // if clinic data daryaft shode bood
            attach = clinicDefaulValue.attachment; // add clinic attachment to var attach
        }
        console.log("loca ************ : ", clinicLocation);
        // check kardan inke aya img ezafe shode ya na
        // console.log("file    ::: " ,localStorage.getItem(FILE_CODE));
        if (localStorage.getItem(FILE_CODE)) {
            // get new clinic img link from local storage
            const img_path = JSON.parse(localStorage.getItem(FILE_CODE)).data.data;
            if (img_path) {
                // add new clinic image to attachment old
                attach[attach.length] = img_path;
                // for live change clinic old data
                var fitData = {...clinicAddress};
                // add new attach to clinic data
                fitData.attachment = attach ? attach : clinicAddress ? clinicAddress.attachment : [];
                fitData.location = clinicLocation;
                setTimeout(() => {
                    // console.log("*********** : ", id);

                    dispatch(UpdateClinicAddress(id, fitData));
                }, 300);
            }
        } else {
            // console.log("else fit data : ", clinicAddress, " // id : ", id);

            let data = {...clinicAddress};
            data.attachment = [];
            data.location = clinicLocation;
            // console.log("data update : ", data);
            dispatch(UpdateClinicAddress(id, data));
        }
    };

    // handle kardan delete img az list attachment
    const handleDelClinicImg = (e, imgLink) => {
        // clinicAddress.attachment.splice(clinicAddress.attachment.indexOf(imgLink), 1);
        if (clinicDefaulValue.attachment) {
            // delete item from attachment array
            clinicDefaulValue.attachment.splice(
                clinicDefaulValue.attachment.indexOf(imgLink),
                1
            );
            // append kardan attachment jadid
            setClinicAddress({
                ...clinicAddress,
                attachment: clinicDefaulValue.attachment,
            });
        }
    };


    const handleDelete = (e, id) => {
        e.preventDefault();
        dispatch(DeleteClinic(id));
    }

    return (
        <div>
            <div id="loader-wrapper">
                <div id="loader"></div>
                <div className="loader-section section-left"></div>
                <div className="loader-section section-right"></div>
            </div>

            <Header/>
            <div id="main">
                <div className="wrapper">
                    <SideBar/>

                    <div id="loading_back">
                        <div className="preloader-wrapper big active" id="loading_progress">
                            <div className="spinner-layer spinner-blue-only">
                                <div className="circle-clipper left">
                                    <div className="circle"></div>
                                </div>
                                <div className="gap-patch">
                                    <div className="circle"></div>
                                </div>
                                <div className="circle-clipper right">
                                    <div className="circle"></div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div id="back_shado"></div>
                    <div className="container">
                        <div className="row">
                            <div className="col s12" style={{marginBottom: 30}}>
                                <div className="col s12">
                                    <div id="breadcrumbs-wrapper">
                                        <div className="container">
                                            <div className="row">
                                                <div className="col s10 m6 l6">
                                                    <h5 className="breadcrumbs-title">Clinic Address</h5>
                                                    <ol className="breadcrumbs">
                                                        <li>
                                                            <NavLink to="/admin">
                                                                <a>Dashboard</a>
                                                            </NavLink>
                                                        </li>

                                                        <li>
                                                            <a className="active">Clinic Address</a>
                                                        </li>
                                                    </ol>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <div className="col s12">
                                    <div className={"container"} id="map-back">
                                        <div id="map">
                                            {console.log("location first data : ", locati)}
                                            {DoctorData.resDocClinic ? (
                                                <Amap
                                                    datas={clinicDefaulValue ?clinicDefaulValue.location : '' }
                                                    mapData={(item) =>
                                                        setClinicLocation(JSON.parse(item))
                                                    }
                                                />
                                            ) : (
                                                <p>please wait...</p>
                                            )}
                                        </div>
                                        <div className="col s12">
                                            <div
                                                className="input-field col s12 l6"
                                                style={{marginTop: 0}}
                                            >
                                                <p style={{color: "#ccc"}}>Clinic Name</p>
                                                <div id="input_back">
                                                    <input
                                                        placeholder="Clinic Name"
                                                        autoComplete="false"
                                                        defaultValue={
                                                            clinicDefaulValue ? clinicDefaulValue.name : ""
                                                        }
                                                        name="name"
                                                        onChange={(e) => HandleClinicAddress(e)}
                                                        required
                                                        type="text"
                                                        className="validate"
                                                    />
                                                </div>
                                                <p id="alert-p">&nbsp;&nbsp;{t("must-field.l")}</p>
                                                {/*<label htmlFor="password">Password</label>*/}
                                            </div>

                                            <div
                                                className="input-field col s12 l6"
                                                style={{marginTop: 0}}
                                            >
                                                <p style={{color: "#ccc"}}>Clinic addresss</p>
                                                <div id="input_back">
                                                    <input
                                                        placeholder="Clinic Address"
                                                        autoComplete="false"
                                                        name="address"
                                                        defaultValue={
                                                            clinicDefaulValue ? clinicDefaulValue.address : ""
                                                        }
                                                        required
                                                        onChange={(e) => HandleClinicAddress(e)}
                                                        type="text"
                                                        className="validate"
                                                    />
                                                </div>
                                                <p id="alert-p">&nbsp;&nbsp;{t("must-field.l")}</p>
                                                {/*<label htmlFor="password">Password</label>*/}
                                            </div>

                                            <div
                                                className="input-field col s12 l6"
                                                style={{marginTop: 0}}
                                            >
                                                <p style={{color: "#ccc"}}>Clinic Phone Number</p>
                                                <div id="input_back">
                                                    <input
                                                        placeholder="Clinic Phone Number"
                                                        autoComplete="false"
                                                        name="phoneNumber"
                                                        defaultValue={
                                                            clinicDefaulValue
                                                                ? clinicDefaulValue.phoneNumber
                                                                : ""
                                                        }
                                                        onChange={(e) => HandleClinicAddress(e)}
                                                        required
                                                        type="text"
                                                        className="validate"
                                                    />
                                                </div>
                                                <p id="alert-p">&nbsp;&nbsp;{t("must-field.l")}</p>
                                                {/*<label htmlFor="password">Password</label>*/}
                                            </div>

                                            {/* <div
                        className="input-field col s12 l6"
                        style={{ marginTop: 0 }}
                      >
                        <p style={{ color: "#ccc" }}>Clinic Postal Code</p>
                        <div id="input_back">
                          <input
                            placeholder="Clinic Postal Code"
                            autoComplete="false"
                            name="postalCode"
                            required
                            defaultValue={
                              clinicDefaulValue
                                ? clinicDefaulValue.postalCode
                                : ""
                            }
                            type="text"
                            onChange={(e) => HandleClinicAddress(e)}
                            className="validate"
                          />
                        </div>
                        <p id="alert-p">&nbsp;&nbsp;{t("must-field.l")}</p>
                        {/*<label htmlFor="password">Password</label>*/}
                                            {/* </div> */}

                                            <div className="col s12">
                                                <div className={"col s12 l6"}>
                                                    <div
                                                        className="file-field input-field"
                                                        style={{
                                                            position: "relative",
                                                            float: "left",
                                                            height: "80px",
                                                            width: "100%",
                                                        }}
                                                    >
                            <span id="uploaded_img_clinic">
                              <div class="progress" id="progress_img_clinic">
                                <div class="indeterminate"></div>
                              </div>
                            </span>

                                                        <div
                                                            className="btn"
                                                            style={{backgroundColor: "#00b8d4 !important"}}
                                                        >
                              <span>
                                <i className="material-icons">attachment </i>
                              </span>
                                                            <input
                                                                onChange={handleChangeFile}
                                                                type="file"
                                                                name="file"
                                                            />
                                                        </div>

                                                        <div className="file-path-wrapper">
                                                            <input
                                                                className="file-path validate"
                                                                type="text"
                                                                placeholder="Upload one or more clinic img"
                                                            />
                                                        </div>

                                                        <div
                                                            id={"progresss"}
                                                            onClick={(e) =>
                                                                selectFile
                                                                    ? upload_file()
                                                                    : M.toast({html: "please select file ..."})
                                                            }
                                                        >
                              <span>
                                <i className={"material-icons"}>upload</i>
                              </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                            <div className="col s12" style={{marginBottom: 20}}>
                                                <p style={{color: "#ccc"}}>Clinic Image</p>

                                                {clinicDefaulValue ? (
                                                    clinicDefaulValue.attachment ? (
                                                        clinicDefaulValue.attachment.length > 0 ? (
                                                            clinicDefaulValue.attachment.map((item) => {
                                                                return (
                                                                    <div
                                                                        className="doc_doctor"
                                                                        style={{
                                                                            position: "relative",
                                                                            overflow: "hidden",
                                                                            height: "auto",
                                                                        }}
                                                                    >
                                                                        {/* {chekType(item) == "img" ? ( */}
                                                                        <img
                                                                            className="materialboxed"
                                                                            src={item}
                                                                            style={{
                                                                                width: "100%",
                                                                                height: "100%",
                                                                                borderRadius: "5px",
                                                                            }}
                                                                        />

                                                                        <span className="delete_img">
                                      <i
                                          className="material-icons"
                                          onClick={(e) =>
                                              handleDelClinicImg(e, item)
                                          }
                                      >
                                        close
                                      </i>
                                    </span>
                                                                    </div>
                                                                );
                                                            })
                                                        ) : (
                                                            <p style={{color: "#ff6d00"}}>
                                                                no file uploaded
                                                            </p>
                                                        )
                                                    ) : (
                                                        ""
                                                    )
                                                ) : (
                                                    ""
                                                )}
                                            </div>

                                            {/* {console.log("dataaaaaaaaaaaacxxxxxx : " , DoctorData.resDocClinic.data)} */}
                                            {DoctorData.resDocClinic ? (
                                                DoctorData.resDocClinic.data.data.length > 0 ? (
                                                    <div className={"row"}>
                                                        <div
                                                            className="col s12 l4 m6"
                                                            style={{marginBottom: "30px"}}
                                                        >
                                                            <button
                                                                onClick={(e) =>
                                                                    handleForm(
                                                                        e,
                                                                        clinicDefaulValue
                                                                            ? clinicDefaulValue.id
                                                                            : ""
                                                                    )
                                                                }
                                                                className="btn waves-effect blue darken-4"
                                                                style={{width: "100%", textTransform: "unset"}}
                                                            >
                                                                update Clinic
                                                            </button>
                                                        </div>

                                                        <div
                                                            className="col s12 l4"
                                                            style={{marginBottom: "30px"}}
                                                        >

                                                        </div>

                                                        <div
                                                            className="col s12 l4 m6"
                                                            style={{marginBottom: "30px"}}
                                                        >
                                                            <button
                                                                onClick={(e) =>
                                                                    handleDelete(
                                                                        e,
                                                                        clinicDefaulValue
                                                                            ? clinicDefaulValue.id
                                                                            : ""
                                                                    )
                                                                }
                                                                className="btn waves-effect red darken-4"
                                                                style={{width: "100%", textTransform: "unset"}}
                                                            >
                                                                Delete Clinic
                                                            </button>
                                                        </div>

                                                    </div>
                                                ) : (
                                                    <div
                                                        className="col s12 l4 m6"
                                                        style={{marginBottom: "30px"}}
                                                    >
                                                        <button
                                                            onClick={(e) =>
                                                                HandleAddClinic(
                                                                    e,
                                                                    DoctorData.GetMe.data
                                                                        ? DoctorData.GetMe.data.data.id
                                                                        : ""
                                                                )
                                                            }
                                                            className="btn waves-effect blue darken-4"
                                                            style={{width: "100%", textTransform: "unset"}}
                                                        >
                                                            Save Clinic
                                                        </button>
                                                    </div>
                                                )
                                            ) : (
                                                ""
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
});
